import { SettingValue, SettingView } from "./SettingView";
import React, { useState } from "react";
import { SplitView, JSWindow } from "@jswf/react";

export function SettingListViewTest() {
  const [values, setValues] = useState<SettingValue[]>([
    { name: "", value: "", type: "text" },
    { name: "", value: "", type: "check" },
    {
      name: "",
      value: "1",
      type: "select",
      options: [
        { label: "", value: "1" },
        { label: "", value: "2" }
      ]
    },
    { name: "", value: "", type: "image" }
  ]);
  return (
    <JSWindow title="" width={800} height={600}>
      <SplitView pos={300}>
        <pre style={{overflow:"auto"}}>{JSON.stringify(values,null,' ')}</pre>
        <SettingView
          values={values}
          onChange={values => {
            setValues(values);
          }}
        />
      </SplitView>
    </JSWindow>
  );
}
