import * as React from "react";
import {
  JSWindow,
  ListView,
  ListHeaders,
  ListHeader,
  ListRow,
  ListItem,
} from "@jswf/react";

export function ListViewSimple() {
  let count = 1;
  return (
    <JSWindow width={600} title="ListViewSimple">
      <ListView>
        <ListHeaders>
          <ListHeader type="number">No</ListHeader>
          <ListHeader width={100}><br /></ListHeader>
          <ListHeader type="number"></ListHeader>
          <ListHeader type="number"></ListHeader>
        </ListHeaders>
        <ListRow>
          <ListItem>{count++}</ListItem>
          <ListItem></ListItem>
          <ListItem>5</ListItem>
          <ListItem>10</ListItem>
        </ListRow>
        <ListRow>
          <ListItem>{count++}</ListItem>
          <ListItem></ListItem>
          <ListItem>18</ListItem>
          <ListItem>120</ListItem>
        </ListRow>
        <ListRow>
          <ListItem>{count++}</ListItem>
          <ListItem></ListItem>
          <ListItem>10</ListItem>
          <ListItem>40</ListItem>
        </ListRow>
        <ListRow>
          <ListItem>{count++}</ListItem>
          <ListItem></ListItem>
          <ListItem>30</ListItem>
          <ListItem>380</ListItem>
        </ListRow>
        <ListRow>
          <ListItem>{count++}</ListItem>
          <ListItem></ListItem>
          <ListItem>40</ListItem>
          <ListItem>700</ListItem>
        </ListRow>
      </ListView>
    </JSWindow>
  );
}
